<template>
  <v-app-bar app color="blue" dark elevate-on-scroll scroll-target="#scrolling-techniques-7">
    <v-app-bar-nav-icon @click.stop="onToggleDrawer"></v-app-bar-nav-icon>
    <v-toolbar-title v-if="title != ''">{{ title }}</v-toolbar-title>
    <v-spacer></v-spacer>
    <div v-if="!token">
      <v-btn text large @click="jump('Login')">登入</v-btn>
      <v-btn depressed large color="red" @click="jump('Register')">注册</v-btn>
    </div>
    <div v-else>
      <v-menu v-model="msg_menu" :close-on-content-click="false" :nudge-width="200" offset-x>
        <template v-slot:activator="{ on }">
          <v-btn v-on="on" text icon color="white">
            <v-badge v-if="msg_state == true" color="red" bottom dot offset-x="8" offset-y="8">
              <v-icon>mdi-bell</v-icon>
            </v-badge>
            <v-icon v-else>mdi-bell</v-icon>
          </v-btn>
        </template>

        <v-card>
          <v-list>
            <v-list-item>
              <v-list-item-avatar>
                <v-icon>mdi-message-processing</v-icon>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title>信息</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>

          <v-divider></v-divider>

          <v-list>
            <div v-if="this.msg_state == true">
              <v-list-item v-for="(item,msg) in msg_list" :key="msg" @click="$router.push({ name: 'msgcontent', params: { id: item._id }})">
                <v-list-item-action>
                  <v-icon>mdi-message</v-icon>
                </v-list-item-action>
                <v-list-item-title>{{ item.msg_title }}</v-list-item-title>
              </v-list-item>
            </div>

            <v-list-item v-else>
              <v-list-item-action>
                <v-icon>mdi-package-variant</v-icon>
              </v-list-item-action>
              <v-list-item-title>当前没有新信息</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-card>
      </v-menu>
    </div>
  </v-app-bar>
</template>

<script>
import { mapState } from "vuex";

import { StateMsg } from "../request/api";

export default {
  data() {
    return {
      fav: true,
      menu: false,
      msg_menu: false,
      msg_state: false,
      message: false,
      hints: true,
      msg_list: [],
      drawer: this.value
    };
  },
  props: {
    replaceProfileWithMenu: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    },
    value: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    ...mapState({
      avatar: state => state.user.avatar,
      username: state => state.user.username,
      email_verify: state => state.user.email_verify,
      token: state => state.user.token,
      mystate: state => state.user.mystate,
      usertype: state => state.user.usertype
    })
  },
  mounted() {
    if (localStorage.getItem("token")) {
      setInterval(this.requestMessage, 10 * 1000);
    }
  },
  watch: {
    $$route() {}
  },
  methods: {

    jump(link) {
      if (this.$route.path != "/" + link) {
        this.$router.push({ path: "/" + link });
      }
      if (!link) {
        this.$router.push({ path: "/" });
      }
    },

    requestMessage() {
      if (localStorage.getItem("token")) {
        StateMsg().then(res => {
          if (res.code == 0) {
            if (res.data.noread == true) {
              this.msg_state = true;
              this.msg_list = res.data.data;
            } else {
              this.msg_state = false;
            }
          } else {
            this.$snackbar.error("拉取信息时出现问题");
          }
        });
      }
    },

    onToggleDrawer() {
      this.drawer = false;
      if (this.drawer === true) {
        this.drawer = false;
      } else {
        this.drawer = true;
      }
      this.$emit("input", this.drawer);
    }
  }
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
， #app {
  width: 100%;
  height: 100%;
}
</style>
