<template>
  <v-app>
    <!-- <v-sheet id="scrolling-techniques-7" class="overflow-y-auto" max-height="100%">
      <v-content>
        <router-view />
      </v-content>
    </v-sheet>-->
    <router-view />
    <offline v-show="offline_state"></offline>
    <v-overlay :value="loading_state">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <!-- <v-footer absolute class="font-weight-medium" app>
      <v-col class="text-center" cols="12">
        © {{ new Date().getFullYear() }} —
        <strong>Vuetify</strong>
      </v-col>
    </v-footer> -->
  </v-app>
</template>

<script>
import { mapState } from "vuex";
import { GetProfile } from "./request/api";
import { autoMeasureBestOne } from "@/helpers/gravatar";
import offline from "./components/offline.vue";

export default {
  name: "App",
  components: {
    offline
  },
  data: () => ({}),
  computed: {
    ...mapState({
      offline_state: state => state.UPDATE_OfflineShow,
      loading_state: state => state.UPDATE_LOADING
    })
  },
  mounted() {
    if (localStorage.getItem("token")) {
      this.$store.commit("pushtoken", localStorage.getItem("token"));
      GetProfile().then(res => {
        localStorage.setItem("username", res.data.username);
        this.$store.commit("pushprofile", res.data);
      });
    }
  },
  watch: {
    $$route() {}
  },
  created() {
    autoMeasureBestOne().then(result => {
      window.console.log(
        `Auto Measure Best Gravatar Server: ${result[0]}, ${result[1]}`
      );
    });
  },
  methods: {
    closesnb() {
      this.$store.commit("closesnackbar");
    },
    jump(link) {
      if (this.$route.path != "/" + link) {
        this.$router.push({ path: "/" + link });
      }
      if (!link) {
        this.$router.push({ path: "/" });
      }
    }
  }
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
， #app {
  width: 100%;
  height: 100%;
}
</style>